export default {
  props: ["img", "description", "tag"],
  data() {
    return {
      active: false,
    };
  },
  mounted() {},
  methods: {
    toggle() {
      this.active = !this.active;

      if (this.active) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.style.overflow = "auto";
      }
    },
    toggleOutside(e) {
      // if (e.target == e.currentTarget)
      this.active = !this.active;

      if (this.active) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.style.overflow = "auto";
      }
    },
  },
};
