var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"w-full"},[_vm._ssrNode("<img"+(_vm._ssrAttr("src",require("assets/images/kdb-t-1.png")))+" alt class=\"block md:hidden kb-img-max-h object-cover w-full\" data-v-6399062e> <img"+(_vm._ssrAttr("src",require("assets/images/KatjaHeroMockup.png")))+" alt class=\"hidden md:block kb-img-max-h object-cover w-full\" data-v-6399062e> <div class=\"p-4 flex flex-col items-center text-center mx-auto my-8\" data-v-6399062e><h1 class=\"main-font text-9xl uppercase\" data-v-6399062e>Katja De Bourbon</h1> <p data-v-6399062e>\n      An artistic <strong data-v-6399062e>portfolio site</strong> for an emotion-driven\n      cinematographer.\n    </p> <div class=\"flex gap-4 py-4\" data-v-6399062e><p class=\"tag\" data-v-6399062e>UI Design</p> <p class=\"tag\" data-v-6399062e>Usability</p></div></div> <div class=\"p-4 kb-max-w my-16\" data-v-6399062e><h2 class=\"overline mb-8\" data-v-6399062e>Project Info</h2> <div class=\"flex flex-col md:flex-row gap-16 md:gap-4\" data-v-6399062e><div class=\"flex-1 flex gap-8 flex-col\" data-v-6399062e><div class=\"flex gap-4\" data-v-6399062e><div class=\"flex-1\" data-v-6399062e><p data-v-6399062e><strong data-v-6399062e>Project Type</strong> <br data-v-6399062e>\n              Freelance\n            </p> <br data-v-6399062e> <p data-v-6399062e><strong data-v-6399062e>Timeline</strong> <br data-v-6399062e>\n              1 month\n            </p> <br data-v-6399062e> <p data-v-6399062e><strong data-v-6399062e>Tools</strong> <br data-v-6399062e>\n              Webflow\n              <br data-v-6399062e>\n              Figma <br data-v-6399062e>\n              Procreate\n            </p></div> <div class=\"flex-1\" data-v-6399062e><p data-v-6399062e><strong data-v-6399062e>Team</strong> <br data-v-6399062e>\n              Solo\n            </p> <br data-v-6399062e> <p data-v-6399062e><strong data-v-6399062e>My Role</strong> <br data-v-6399062e>\n              UI Designer\n            </p></div></div></div> <div class=\"flex-1\" data-v-6399062e>\n        With cinematographer Katja de Bourbon, I designed and developed a\n        portfolio that showcases her creativity and vision.\n      </div></div></div> <div class=\"p-4 my-16 kb-max-w\" data-v-6399062e><h2 class=\"overline mb-8\" data-v-6399062e>Overview</h2> <div class=\"flex flex-col md:flex-row gap-8\" data-v-6399062e><h3 class=\"flex-1 heading\" data-v-6399062e>Showcasing artistic talent.</h3> <p class=\"flex-1\" data-v-6399062e>\n        The main goal of this project was to help the client stand out among\n        the sea of other portfolio and help her gain more job opportunities.\n        When concepting, I was sure to capture her style in a way that would\n        be unique to her while still being easy to navigate for potential\n        employers or collaborators.\n      </p></div></div> <div class=\"flex flex-col md:flex-row gap-16 items-center my-16\" data-v-6399062e><div class=\"flex-1\" data-v-6399062e><img"+(_vm._ssrAttr("src",require("assets/images/KatjaProblem.png")))+" alt class=\"kb-img-max-h object-contain\" data-v-6399062e></div> <div class=\"flex-1 p-4\" data-v-6399062e><h2 class=\"overline mb-4\" data-v-6399062e>The Problem</h2> <p class=\"subheading-2\" style=\"max-width: 620px\" data-v-6399062e>\n        How might we showcase the clients talent and creativity in a way that\n        stands out against the sea of other portfolios?\n      </p></div></div> <div class=\"p-4 kb-mw-800\" data-v-6399062e><div class=\"subheading mb-4\" data-v-6399062e>Competitive Analysis</div> <p data-v-6399062e>\n      Before designing, I looked at numerous film portfolios to get a lay of\n      the land. Many looked minimalistic and clean; almost like a museum\n      gallery. There was a great opportunity to express my client's creativity\n      and personality with the design.\n    </p></div> <div class=\"p-4 kb-max-w my-16\" data-v-6399062e><div class=\"subheading mb-4 text-center\" data-v-6399062e>Moodboard</div> <img"+(_vm._ssrAttr("src",require("assets/images/KatjaMoodboard.png")))+" alt class=\"kb-img-max-h my-6\" data-v-6399062e> <p class=\"kb-mw-800 mt-8\" data-v-6399062e>\n      During the initial consult call, I worked with Katja to better\n      understand her vision and what she wanted for her portfolio. She created\n      this moodboard with a wide variety of inspiration. She loved the\n      aesthetic of having film reel and collage and heavily drifted towards\n      mixed media, which I used as a running visual theme going into\n      development.\n    </p></div> <div class=\"my-16 p-4 kb-max-w\" data-v-6399062e><h2 class=\"overline mb-8\" data-v-6399062e>Wireframes</h2> <h3 class=\"heading\" data-v-6399062e>Blocking out the content.</h3> <img"+(_vm._ssrAttr("src",require("assets/images/KatjaWireframes.png")))+" alt class=\"kb-img-max-h my-6\" data-v-6399062e></div> <div class=\"my-16 p-4 kb-max-w\" data-v-6399062e><h2 class=\"overline mb-8\" data-v-6399062e>Usability Testing</h2> <div class=\"flex flex-col md:flex-row gap-16\" data-v-6399062e><div class=\"flex-1\" data-v-6399062e><h3 class=\"heading\" data-v-6399062e>Blocking out the content.</h3></div> <div class=\"flex-1\" data-v-6399062e><p data-v-6399062e>\n          I created a prototype using Figma and conducted usability tests with\n          four film industry professionals, asking them to complete various\n          tasks and give feedback to help improve the design.\n        </p></div></div> <div class=\"flex flex-col md:flex-row gap-16 my-32\" data-v-6399062e><div class=\"flex-1\" data-v-6399062e><img"+(_vm._ssrAttr("src",require("assets/images/KatjaUserTest.png")))+" alt class=\"kb-img-max-h\" data-v-6399062e></div> <div class=\"flex-1\" data-v-6399062e><div class=\"subheading mb-4\" data-v-6399062e>Key User Insights</div> <p class=\"leading-8 my-4\" data-v-6399062e>\n          01\n          <br data-v-6399062e> <strong data-v-6399062e>The CV should have its own tab</strong> <br data-v-6399062e>\n          The CV is an essential document to those hiring. It felt hidden on\n          the bottom of the About page.\n        </p> <p class=\"leading-8 my-4\" data-v-6399062e>\n          02\n          <br data-v-6399062e> <strong data-v-6399062e>Typographic hierarchy on CV needs improvement</strong> <br data-v-6399062e>\n          Need differentiation between film titles and rest of text to make it\n          easier to scan.\n        </p> <p class=\"leading-8 my-4\" data-v-6399062e>\n          03\n          <br data-v-6399062e> <strong data-v-6399062e>Looks pretty and is easy to use</strong> <br data-v-6399062e>\n          All participants expressed positive feedback towards the entire\n          site's aesthetic.\n        </p></div></div></div> <div class=\"my-16 p-4 kb-max-w\" data-v-6399062e><h2 class=\"overline mb-8\" data-v-6399062e>Design System</h2> <h3 class=\"heading\" data-v-6399062e>Utilizing systems to reduce build time.</h3> <img id=\"featured-photo\""+(_vm._ssrAttr("src",require("assets/images/Katja De Bourbon Design System 1.png")))+" alt class=\"kb-img-max-h my-6\" data-v-6399062e> <div id=\"feature-carousel\" class=\"flex gap-4 justify-center items-center\" data-v-6399062e><img"+(_vm._ssrAttr("src",require("assets/images/Katja De Bourbon Design System 1.png")))+" alt class=\"carousel-photo\" data-v-6399062e> <img"+(_vm._ssrAttr("src",require("assets/images/Katja De Bourbon Design System 2.png")))+" alt class=\"carousel-photo\" data-v-6399062e> <img"+(_vm._ssrAttr("src",require("assets/images/Katja De Bourbon Design System 3.png")))+" alt class=\"carousel-photo\" data-v-6399062e> <img"+(_vm._ssrAttr("src",require("assets/images/Katja De Bourbon Design System 4.png")))+" alt class=\"carousel-photo\" data-v-6399062e> <img"+(_vm._ssrAttr("src",require("assets/images/Katja De Bourbon Design System 5.png")))+" alt class=\"carousel-photo\" data-v-6399062e> <img"+(_vm._ssrAttr("src",require("assets/images/Katja De Bourbon Design System 6.png")))+" alt class=\"carousel-photo\" data-v-6399062e></div> <div class=\"subheading my-16 text-center\" data-v-6399062e>Doodle System</div> <div class=\"md:w-6/12 my-8 p-4 mx-auto\" data-v-6399062e><p data-v-6399062e><strong data-v-6399062e>Crafty details to bring out personality</strong> <br data-v-6399062e>I\n        created a series of doodles using Procreate to add a personalized\n        touch to the pages and emphasize the collage quality desired by the\n        client.\n      </p></div> <img"+(_vm._ssrAttr("src",require("assets/images/KatjaDoodleSystem.png")))+" alt class=\"kb-img-max-h w-full object-contain my-8\" data-v-6399062e> <p class=\"text-center grey mt-2\" data-v-6399062e>Doodles</p> <div class=\"flex flex-wrap gap-4 justify-between items-center\" data-v-6399062e><div class=\"doodle-item\" data-v-6399062e><img"+(_vm._ssrAttr("src",require("assets/images/KatjaSignature.png")))+" alt class=\"kb-img-max-h my-6\" data-v-6399062e> <p class=\"text-center grey mt-2\" data-v-6399062e>Client's signature</p></div> <div class=\"doodle-item\" data-v-6399062e><p data-v-6399062e><strong data-v-6399062e>Adding the clients personal touch</strong> <br data-v-6399062e>To push the\n          collage theme even further, I added the clients signature and their\n          own scanned pressed flowers to the pages about them.\n        </p></div> <div class=\"w-full text-center\" data-v-6399062e><img"+(_vm._ssrAttr("src",require("assets/images/KatjaFlowers.png")))+" alt class=\"kb-img-max-h w-full object-contain my-6\" data-v-6399062e> <p class=\"text-center grey mt-2\" data-v-6399062e>Scans of handpressed flowers</p></div></div></div> <div class=\"my-16 p-4 kb-max-w\" data-v-6399062e><h2 class=\"overline mb-8\" data-v-6399062e>Final Design</h2> <div class=\"flex flex-col md:flex-row gap-16\" data-v-6399062e><div class=\"flex-1\" data-v-6399062e><h3 class=\"heading\" data-v-6399062e>A fully functional portfolio.</h3></div> <div class=\"flex-1\" data-v-6399062e><p data-v-6399062e>\n          I delivered a fully functional Webflow portfolio site for my client,\n          along with a maintenance guide to allow them to make updates and\n          changes easily without my guidance after the project was complete.\n        </p></div></div> <div class=\"my-16\" data-v-6399062e><div class=\"video-container\" data-v-6399062e><iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/r_hpw8gjbD0\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"allowfullscreen\" data-v-6399062e></iframe></div></div> <img"+(_vm._ssrAttr("src",require("assets/images/KatjaSpreadMockup1.png")))+" alt class=\"kb-img-max-h object-cover w-full my-8\" data-v-6399062e> <img"+(_vm._ssrAttr("src",require("assets/images/KatjaSpreadMockup2.png")))+" alt class=\"object-cover w-full my-8\" data-v-6399062e></div> <div class=\"my-16 p-4 kb-max-w\" data-v-6399062e><h2 class=\"overline mb-8\" data-v-6399062e>Reflection</h2> <h3 class=\"heading\" data-v-6399062e>Systems can help push exploration forward.</h3> <p class=\"mt-8\" data-v-6399062e>\n      I loved incorporating traditional media into the site. I wish I had\n      explored further with the visual assets the client gave me to create a\n      diverse set of conceptsâ€”it feels like a missed opportunity.\n    </p> <p class=\"mt-8\" data-v-6399062e>\n      Start with visual exploration and iterating concepts, then move to\n      creating a design system instead of doing it after the design was\n      created in high-fidelity. It would've allowed me to iterate much faster\n      and identify issues with hierarchy (for typography and buttons) quickly.\n    </p> <p class=\"mt-8\" data-v-6399062e>\n      I have been interested in design systems for the past couple of years.\n      However, upon reflecting on this particular project, I don't think I\n      understood how to use design systems effectively. Seeing them more as a\n      way to document styling for development and for myself to refer to when\n      designing, I was not taking advantage of its capabilities to speed up\n      prototype iteration and its usefulness to the client after the project\n      has ended.\n    </p></div> "),_c('nuxt-link',{attrs:{"to":"/hackville"}},[_c('div',{staticClass:"flex justify-end text-right flex-col p-4"},[_c('div',{staticClass:"text-lg"},[_vm._v("Next Project")]),_vm._v(" "),_c('div',{staticClass:"text-8xl main-font uppercase"},[_vm._v("Hackville 2020")])])])],2)}
